import { definePicketByMeter, anyToNumber } from 'src/library/helpers/numbers';
import { IRetreat, IKilometer, ICargo } from 'src/state/redux/features/RCDMRostov/FreightTension/actionCreators';

export const sheetRetreatsToObj = (xlsxData: any): IRetreat[] => {
  let returnedArr: IRetreat[] = [];
  let id = 0;

  xlsxData.forEach((item: any) => {
    if (
      item[''] < 99999 &&
      (item['?'] === 0 || item['?'] === undefined) &&
      (item[''] === 0 || item[''] === undefined) &&
      (item['EXCLUDE'] === 0 || item[''] === undefined)
    ) {
      returnedArr.push({
        id: ++id,
        distanceNumber: anyToNumber(+item['']),
        year: anyToNumber(+item['']),
        month: anyToNumber(+item['']),
        day: anyToNumber(+item['']),
        diagnosticToolNumber: anyToNumber(+item['']),
        checkType: anyToNumber(+item['']),
        directionCode: anyToNumber(+item['']),
        trackNumber: item[''],
        kilometer: anyToNumber(+item['KM']),
        picket: anyToNumber(definePicketByMeter(item['']) ?? 0),
        meter: anyToNumber(+item['']),
        retreatName: item[''],
        retreatDegree: anyToNumber(+item['']),
        retreatAmplitude: item[''],
        retreatLength: item[''],
        retreatScore: anyToNumber(+item[''] ?? 0),
        retreatCount: anyToNumber(+item['']),
        passLimitSpeed: item['__'] !== '-' || item['__'] !== undefined ? item['__'] : null,
        freightLimitSpeed:
          item['__'] !== '-' || item['__'] !== undefined ? item['__'] : null,
        passSetSpeed: item['__'] !== '-' || item['__'] !== undefined ? item['__'] : null,
        reightSetSpeed: item['__'] !== '-' || item['__'] !== undefined ? item['__'] : null,
        norm: anyToNumber(+item['HOPMA']),
        subgrade: anyToNumber(+item['']),
        insulatingGap: anyToNumber(+item['']),
        bridge: anyToNumber(+item['']),
        prPredupr: anyToNumber(+item['PR_PREDUPR']),
      });
    }
  });

  return returnedArr;
};

export const sheetKilometersToObj = (xlsxData: any): IKilometer[] => {
  let returnedArr: IKilometer[] = [];
  let id = 0;

  xlsxData.forEach((item: any) => {
    if (item[''] < 99999) {
      returnedArr.push({
        id: ++id,
        distanceNumber: item[''],
        year: item[''],
        month: item[''],
        day: item[''],
        diagnosticToolNumber: item[''],
        checkType: item[''],
        directionCode: item[''],
        trackNumber: item[''],
        kilometer: item['KM'],
        // picket: definePicketByMeter(item['M']) ?? 0,
        // meter: item['M'],
        grade: item[''],
        score: item[''],
        checked: item[''],
        passLimitSpeed: item['__'] !== '-' ? item['__'] : null,
        freightLimitSpeed: item['__'] !== '-' ? item['__'] : null,
        passSetSpeed: item['__'] !== '-' ? item['__'] : null,
        reightSetSpeed: item['__'] !== '-' ? item['__'] : null,
      });
    }
  });

  return returnedArr;
};

export const sheetCargosToObj = (xlsxData: any): ICargo[] => {
  function getDistanceNumber(distanceStr: string) {
    const distanceSubStr = distanceStr.substr(0, 5);

    if (distanceSubStr.substr(0, 2) === '') {
      if (+distanceSubStr.substr(3, 1) === 1) {
        return 20;
      } else if (+distanceSubStr.substr(3, 1) === 2) {
        return 2;
      } else if (+distanceSubStr.substr(3, 1) === 3) {
        return 28;
      }
    }

    if (distanceSubStr.substr(0, 2) === '') {
      return +distanceSubStr.substr(3, 2);
    }

    return 0;
  }

  let returnedArr: ICargo[] = [];
  let id = 0;

  xlsxData.forEach((item: any) => {
    returnedArr.push({
      id: ++id,
      distanceNumber: getDistanceNumber(item['']),
      directionCode: item[''],
      trackNumber: item[''],
      cargoDegree: item[' '],
      startKm: item[' '],
      startMeter: item[' '],
      endKm: item[' '],
      endMeter: item[' '],
    });
  });

  return returnedArr;
};
