import {
  countOfFilledRows as countOfFilledRowsFunction,
  lastFilledRowNumber as lastFilledRowNumberFunction,
} from 'src/library/helpers/xlsx';

class FileValidator {
  isCorrectFileType(type: string | undefined) {
    const validTypes = [
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      'application/vnd.ms-excel',
    ];

    if (!type) {
      return false;
    }

    if (validTypes.includes(type)) {
      return true;
    } else {
      return false;
    }
  }

  missingSheets(validateSheets: string[], neededSheets: string[]): string[] {
    let missingSheets: string[] = [];

    neededSheets.forEach((item) => {
      if (!validateSheets.includes(item)) {
        missingSheets.push(item);
      }
    });

    return missingSheets;
  }

  /**
   *          
   *
   * @param {Object} parsedObject -   excel     XLSX
   * @param {String} columnLetter -    "A"
   * @returns {string[]}
   */
  emptyCellsInColumn(parsedObject: any, columnLetter: string): string[] {
    let emptyCellsInColumn: string[] = [];
    const countOfFilledRows = countOfFilledRowsFunction(parsedObject, columnLetter);
    const lastFilledRowNumber = lastFilledRowNumberFunction(parsedObject, columnLetter);

    if (countOfFilledRows !== lastFilledRowNumber) {
      for (let num = 1; num <= lastFilledRowNumber; num++) {
        if (!parsedObject[`${columnLetter}${num}`]) {
          emptyCellsInColumn.push(`${columnLetter}${num}`);
        }
      }
    }

    return emptyCellsInColumn;
  }

  /**
   *                  
   *
   * @param {Object} parsedObject -   excel     XLSX
   * @param {String} columnLetter -    "A"
   * @returns {string[]}
   */
  emptyCellsInColumnLoopCountColumnA(parsedObject: any, columnLetter: string): string[] {
    let emptyCellsInColumnLoopCountColumnA: string[] = [];
    const lastFilledRowNumber = lastFilledRowNumberFunction(parsedObject, 'A');

    for (let num = 2; num <= lastFilledRowNumber; num++) {
      if (!parsedObject[`${columnLetter}${num}`]) {
        emptyCellsInColumnLoopCountColumnA.push(`${columnLetter}${num}`);
      }
    }

    return emptyCellsInColumnLoopCountColumnA;
  }

  /**
   *              type
   *
   * @param {string} type -   
   * @param parsedObject
   * @param columnLetter
   * @returns
   */
  allCellsInColumnMustBeTypeOf(type: string, parsedObject: any, columnLetter: string) {
    let returnedArr: string[] = [];

    const lastFilledRowNumber = lastFilledRowNumberFunction(parsedObject, columnLetter);

    for (let num = 2; num <= lastFilledRowNumber; num++) {
      if (typeof parsedObject[`${columnLetter}${num}`]?.v !== type) {
        returnedArr.push(`${columnLetter}${num}`);
      }
    }

    return returnedArr;
  }

  /**
   *         ""
   *
   * @param parsedObject
   */
  sheetFreightCellsEmptyValues(parsedObject: any) {
    let returnedArr: string[] = [];

    let emptyCells = this.emptyCellsInColumnLoopCountColumnA(parsedObject, 'B');
    returnedArr = [...returnedArr, ...emptyCells];

    emptyCells = this.emptyCellsInColumnLoopCountColumnA(parsedObject, 'J');
    returnedArr = [...returnedArr, ...emptyCells];

    emptyCells = this.emptyCellsInColumnLoopCountColumnA(parsedObject, 'K');
    returnedArr = [...returnedArr, ...emptyCells];

    emptyCells = this.emptyCellsInColumnLoopCountColumnA(parsedObject, 'L');
    returnedArr = [...returnedArr, ...emptyCells];

    return returnedArr;
  }

  sheetRetreatsHeader(retreatSheet: any) {
    let returnedArr: string[] = [];

    if (retreatSheet.B1?.v !== '') returnedArr.push('B1');
    if (retreatSheet.C1?.v !== '') returnedArr.push('C1');
    if (retreatSheet.D1?.v !== '') returnedArr.push('D1');
    if (retreatSheet.E1?.v !== '') returnedArr.push('E1');
    if (retreatSheet.F1?.v !== '') returnedArr.push('F1');
    if (retreatSheet.G1?.v !== '') returnedArr.push('G1');
    if (retreatSheet.H1?.v !== '') returnedArr.push('H1');
    if (retreatSheet.I1?.v !== '') returnedArr.push('I1');
    if (retreatSheet.L1?.v !== 'KM') returnedArr.push('L1');
    if (retreatSheet.M1?.v !== '') returnedArr.push('M1');
    if (retreatSheet.N1?.v !== '') returnedArr.push('N1');
    if (retreatSheet.O1?.v !== '') returnedArr.push('O1');
    if (retreatSheet.P1?.v !== '') returnedArr.push('P1');
    if (retreatSheet.Q1?.v !== '') returnedArr.push('Q1');
    if (retreatSheet.R1?.v !== '') returnedArr.push('R1');
    if (retreatSheet.S1?.v !== '') returnedArr.push('S1');
    if (retreatSheet.T1?.v !== '__') returnedArr.push('T1');
    if (retreatSheet.U1?.v !== '__') returnedArr.push('U1');
    if (retreatSheet.V1?.v !== '__') returnedArr.push('V1');
    if (retreatSheet.W1?.v !== '__') returnedArr.push('W1');
    if (retreatSheet.X1?.v !== 'HOPMA') returnedArr.push('X1');
    if (retreatSheet.Y1?.v !== '') returnedArr.push('Y1');
    if (retreatSheet.Z1?.v !== '') returnedArr.push('Z1');
    if (retreatSheet.AA1?.v !== '?') returnedArr.push('AA1');
    if (retreatSheet.AB1?.v !== '') returnedArr.push('AB1');
    if (retreatSheet.AD1?.v !== '') returnedArr.push('AD1');
    if (retreatSheet.AE1?.v !== 'PR_PREDUPR') returnedArr.push('AE1');
    if (retreatSheet.AF1?.v !== 'EXCLUDE') returnedArr.push('AF1');

    return returnedArr;
  }

  sheetKilometersHeader(kilometersSheet: any) {
    let returnedArr: string[] = [];

    if (kilometersSheet.B1.v !== '') returnedArr.push('B1');
    if (kilometersSheet.C1.v !== '') returnedArr.push('C1');
    if (kilometersSheet.D1.v !== '') returnedArr.push('D1');
    if (kilometersSheet.E1.v !== '') returnedArr.push('E1');
    if (kilometersSheet.F1.v !== '') returnedArr.push('F1');
    if (kilometersSheet.G1.v !== '') returnedArr.push('G1');
    if (kilometersSheet.H1.v !== '') returnedArr.push('H1');
    if (kilometersSheet.I1.v !== '') returnedArr.push('I1');
    if (kilometersSheet.O1.v !== 'KM') returnedArr.push('O1');
    if (kilometersSheet.P1.v !== 'M') returnedArr.push('P1');
    if (kilometersSheet.Q1.v !== '') returnedArr.push('Q1');
    if (kilometersSheet.R1.v !== '') returnedArr.push('R1');
    if (kilometersSheet.S1.v !== '') returnedArr.push('S1');
    if (kilometersSheet.T1.v !== '__') returnedArr.push('T1');
    if (kilometersSheet.U1.v !== '__') returnedArr.push('U1');
    if (kilometersSheet.Y1.v !== '__') returnedArr.push('Y1');
    if (kilometersSheet.Z1.v !== '__') returnedArr.push('Z1');

    return returnedArr;
  }

  sheetFreightHeader(freightSheet: any) {
    let returnedArr: string[] = [];

    if (freightSheet.A1.v !== '') returnedArr.push('A1');
    if (freightSheet.B1.v !== '') returnedArr.push('B1');
    if (freightSheet.C1.v !== ' ') returnedArr.push('C1');
    if (freightSheet.D1.v !== ' ') returnedArr.push('D1');
    if (freightSheet.E1.v !== ' ') returnedArr.push('E1');
    if (freightSheet.F1.v !== ' ') returnedArr.push('F1');
    if (freightSheet.G1.v !== ' ') returnedArr.push('G1');
    if (freightSheet.H1.v !== '') returnedArr.push('H1');
    if (freightSheet.I1.v !== '') returnedArr.push('I1');
    if (freightSheet.J1.v !== '') returnedArr.push('J1');
    if (freightSheet.K1.v !== '') returnedArr.push('K1');
    if (freightSheet.L1.v !== ' ') returnedArr.push('L1');

    return returnedArr;
  }

  sheetsValidate(sheetNames: any): string[] {
    let newErrors: string[] = [];
    /* ----------------    ------------------ */
    const missingSheets = this.missingSheets(sheetNames, ['', '2.', '3.', '4.', ' ']);
    if (missingSheets.length) {
      newErrors.push('     : ' + missingSheets.join(', '));
      return newErrors;
    }

    return newErrors;
    /* ---------------- /    ------------------ */
  }
}

export default FileValidator;
