/**
 *        
 * @param xlsxData -  .      XLSX   
 * @returns {number}
 */
export function defineSheetLength(xlsxData: any): number {
  let length: number = 0;
  const keys = Object.keys(xlsxData);

  keys.forEach((item) => {
    const regexp = /\d+/;
    const num: number = Number(item.match(regexp)) ?? 0;

    if (length < num) length = num;
  });

  return length;
}
