import { dotInsteadComma } from 'src/library/helpers/numbers';
import { IRetreat, IData, ICheckedDistance, ICumulativeGap } from 'src/state/redux/features/video/actionCreators';

export const sheetRetreatsToObj = (xlsxData: any): IRetreat[] => {
  let returnedArr: IRetreat[] = [];

  returnedArr = xlsxData.map((item: any) => {
    /*    */
    let distanceNumber = null;

    if (String(item['']).match(/-/g)) {
      distanceNumber = item[''].match(/\d+/g);
    } else if (String(item['']).match(/-/g)) {
      if (item[''].match(/\d+/g)[0] === '2') {
        distanceNumber = ['2'];
      } else if (item[''].match(/\d+/g)[0] === '1') {
        distanceNumber = ['20'];
      } else if (item[''].match(/\d+/g)[0] === '3') {
        distanceNumber = ['28'];
      }
      /*           3 */
    } else {
      distanceNumber = [`${item['']}`];
    }

    return {
      id: item['  '],
      directionCode: item[' '],
      stationOrLine: item[' / '],
      distanceNumber: +distanceNumber[0],
      trackNumber: item[''],
      kilometer: item[''],
      picket: item[''],
      meter: item[''],
      thread: item[''],
      retreatSize: item[''],
      pad: item['  '],
      limitSpeed: item['. '],
      setSpeed: item['. '],
      retreatCode: item['  (    )'],
      regionNumber: item[''],
      checkType: item['  (, , )'],
      curveRadius: item[' '],
      subrailBase: item['  /'],
      trackType: item['  (./ )'],
      warningNumber: item[' '],
    };
  });

  return returnedArr;
};

export const sheetDataToObj = (xlsxData: any): IData => {
  const data = xlsxData[0];
  let returnedObj = {
    checkDate: data[' '],
    decryptionDate: data[' '],
    inspectionArea:
      data['  (    1  ,    2  )'],
    diagnosticToolCode: data['   (      )'],
    diagnosticToolNumber: data['  '],
    checkedMainTracksKm: data['  , '],
    checkedSideTracksKm: data['  , '],
    checkType: data[' '],
  };

  return returnedObj;
};

export const sheetCumulativeGapsToObj = (
  xlsxData: any,
  cumulativeGapsFromStateTemplate: ICumulativeGap[]
): ICumulativeGap[] => {
  let returnedArr: ICumulativeGap[] = [];

  returnedArr = xlsxData.map((item: any, index: number) => {
    return {
      distanceName: cumulativeGapsFromStateTemplate[index].distanceName,
      distanceNumber: cumulativeGapsFromStateTemplate[index].distanceNumber,
      gapsCount: item[', .'] ?? null,
      average: item['-, .'] ?? null,
      gapThenOf35: item[' 35 , .'] ?? null,
      gap3135: item['31-35 , .'] ?? null,
      gap2730: item['27-30 , .'] ?? null,
      gap2526: item['25-26 , .'] ?? null,
      repeat: item[' , .'] ?? null,
    };
  });

  return returnedArr;
};

export const sheetCheckedDistances = (xlsxData: any): ICheckedDistance[] => {
  let returnedArr = [];

  returnedArr = xlsxData.map((item: any) => {
    return {
      distanceNumber: item['   '],
      kilometers: +dotInsteadComma(item['     ( )']),
    };
  });

  return returnedArr;
};
